:root {
  --colorBackground: #f4f4f4;
  --colorTextDark: #111321;
  --colorTextLight: #f4f4f4;
  --toglerMainColor: #4440f6;
  --toglerMinorColor: #fff;
  --toglerIconSunColor: #4b48db;
  --toglerIconMoonColor: #5f6775;
  --colorCategoriesBackgroundSelect: #f8f8f8;
  --colorCategoriesBtnBorder: #4440f7;
  --colorTextCategoryBtn: #4b48db;
}

* {
  margin: 0;
  padding: 0;
}

body {
  color: var(--colorTextDark);
  background-color: var(--colorBackground);
  font-family: Manrope, sans-serif;
  font-size: 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  display: block;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

button {
  font-family: Manrope, sans-serif;
}

body.dark {
  --colorBackground: #2e2e2e;
  --colorTextDark: #f4f4f4;
  --colorTextLight: #f4f4f4;
  --toglerMainColor: #fff;
  --toglerMinorColor: #4440f6;
  --toglerIconSunColor: #5f6775;
  --toglerIconMoonColor: #4b48db;
  --colorTextDarkGray: #111321;
}

.container {
  padding: 0 16px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 100;
  background-color: #000000a2;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .authorization {
  transform: translate(-50%, -50%)scale(.9);
}

.autorize__container {
  background-color: var(--colorBackground);
  width: 320px;
  border-radius: 10px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

.autorize__container.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.autorize__nav {
  width: 100%;
  transition: all .5s;
  display: flex;
}

.autorize__nav.hide {
  transform: translateY(-100px);
}

.autorize__nav-item {
  text-transform: uppercase;
  color: #ddd;
  background-color: #b1b1b1;
  flex-basis: 50%;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.autorize__nav-btn {
  width: 100%;
  text-align: center;
  color: currentColor;
  cursor: pointer;
  padding: 10px 0;
  display: block;
}

.autorize__nav-item.active {
  color: var(--colorTextDark);
  background-color: var(--colorBackground);
}

.autorize__form-box {
  width: 100%;
  height: 404px;
  display: flex;
  position: relative;
  overflow: visible;
}

.autorize__form-signin, .autorize__form-signup {
  opacity: 1;
  visibility: visible;
  width: 100%;
  height: 100%;
  flex-direction: column;
  padding: 37px;
  font-size: 16px;
  font-weight: 300;
  transition: visibility .5s, opacity .5s, transform .5s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.autorize__form-signin > :nth-last-child(2), .autorize__form-signup > :nth-last-child(2) {
  margin-bottom: auto;
}

.autorize__form-signin.hide, .autorize__form-signup.hide {
  visibility: hidden;
  opacity: 0;
}

.autorize__form-signin-left {
  transform: translateX(-100%);
}

.autorize__form-signup {
  transform: translateX(100%);
}

.autorize__form-signup-left {
  transform: translateX(0);
}

.autorize__field {
  text-transform: uppercase;
  color: var(--colorTextDark);
  margin-bottom: 10px;
  margin-left: 15px;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  display: block;
  position: relative;
}

.autorize__password-access {
  width: 35px;
  height: 35px;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 8px;
}

.autorize__icon {
  pointer-events: none;
}

.autorize__icon .autorize__use {
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
}

.autorize__use {
  opacity: 0;
}

.autorize__use.show {
  opacity: 1;
}

.autorize__input-box {
  margin-bottom: 10px;
  position: relative;
}

.autorize__input {
  width: 100%;
  height: 35px;
  border: 1px solid var(--colorTextDark);
  border-radius: 20px;
  padding-left: 15px;
}

.autorize__field.togler {
  align-items: center;
  gap: 20px;
  padding-top: 23px;
  display: flex;
}

.autorize__field.togler > span {
  margin-bottom: 0;
}

.autorize__btn-signin, .autorize__btn-signup, .autorize__btn-close {
  color: #fff;
  width: 100%;
  max-width: 246px;
  height: 35px;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #0f4fe6;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  transition: transform .5s;
  display: flex;
}

.autorize__btn-signin.hide, .autorize__btn-signup.hide {
  transform: translateY(100px);
}

.autorize__welcome {
  width: 100%;
  height: 200px;
  opacity: 0;
  color: #0f4fe6;
  background-color: #6a93a0;
  flex-direction: column;
  align-items: center;
  padding: 30px 37px;
  transition: opacity .5s, transform .5s;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}

.autorize__welcome.show {
  color: #232b55bf;
  opacity: 1;
  transform: translateY(0);
}

.autorize__title {
  color: var(--colorTextDark);
  text-align: center;
  flex-grow: 1;
  font-size: 30px;
  font-weight: 300;
}

.autorize__profile-photo {
  height: 150px;
  width: 150px;
  opacity: 0;
  background-image: url("avatar.656d5cd3.jpg");
  background-size: cover;
  border-radius: 50%;
  margin: 0 auto;
  transition: opacity .5s, transform .5s;
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  transform: translateY(calc(-100% - 60px));
}

.autorize__profile-photo.show {
  opacity: 1;
  transform: translateY(0);
}

.autorize__anime {
  width: 100%;
  height: 100%;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
}

.autorize__anime svg {
  width: 160px;
  height: 160px;
  margin: 0 auto;
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
}

.autorize__anime.show path {
  stroke: var(--colorTextDark);
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000px;
  animation: 5s linear forwards dash;
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

.autorize__togler {
  width: 60px;
  height: 26px;
  background-color: #fff;
  border: 2px solid #b1b1b1;
  border-radius: 13px;
  justify-content: space-between;
  align-items: center;
  padding-left: 8px;
  padding-right: 8px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

.autorize__lever {
  width: 18px;
  height: 18px;
  background-color: #b1b1b1;
  border-radius: 50%;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 2px;
  left: 2px;
}

.yes {
  color: #4440f6;
  opacity: 0;
  font-size: 12px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.no {
  color: #474747;
  opacity: 1;
  font-size: 12px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.autorize__chk-box:checked + .autorize__togler {
  border-color: #4440f6;
}

.autorize__chk-box:checked + .autorize__togler .yes {
  opacity: 1;
}

.autorize__chk-box:checked + .autorize__togler .no {
  opacity: 0;
}

.autorize__chk-box:checked + .autorize__togler .autorize__lever {
  background-color: #4440f6;
  transform: translateX(34px);
}

